@import "mixins.scss";
@import "varibales.scss";

.product-container {
  padding: 25px 15px 0;
  margin-left: 16px;
  margin-bottom: 15px;
  min-width: 538px;

  &:last-child {
    margin-bottom: 0;
  }

  .product-row {
    display: flex;
    justify-content: space-between;
    margin-bottom: 25px;
  }
}

.input-block {
  display: flex;
  flex-direction: column;
  max-width: 290px;
  width: 100%;
  margin: 0 10px;

  .product-btn {
    max-width: 200px;
  }

  label {
    font-size: 12px;
    color: $text2C;
    margin-bottom: 5px;
  }
}

.product-faq {

  span {
    margin-bottom: 10px;
  }

  p {
    display: none;
  }
}

.product-label--sub {
  margin-bottom: 0 !important;
  margin-top: 5px;
}

#question-p-1 {
  margin-bottom: 10px;
}

.product-table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 25px;

  thead tr {
    color: $text2C;
  }

  tr {
    border-bottom: 1px solid $border;
  }

  th, td {
    padding: 7px;
  }
}
